      border: solid 2px rgba(255,255,255,.5);
      color: rgb(255,255,255)!important;

      &:hover {
        border: solid 2px rgba(255,255,255,1);
      }
    }
  }

  &.btn-rounded {
    border-radius: 60px;
  }

  &.btn-lg {
    padding: 11px 25px;
  }

  &.btn-sm {
    padding: 7px 25px;
  }

  &.btn-xs {
    padding: 3px 25px;
  }

  &.btn-icon {
    i {
      padding-right: 6px;
      font-size: 14px;
    }

    &.right-icon {
      i {
        padding-left: 6px;
      }
    }

    &.left-icon {
      i {
        padding-right: 6px;
      }
    }
  }

  &.btn-lable-wrap {
    position: relative;

    .btn-label {
      background: rgba(0, 0, 0, 0.05) none repeat scroll 0 0;
      padding: 11px 15px;
      position: absolute;
      top: -2px;

      i {
        font-size: 14px;
      }
    }

    &.left-label {
      .btn-label {
        left: -2px;
      }

      .btn-text {
        margin-left: 35px;
      }
    }

    &.right-label {
      .btn-label {
        right: -2px;
      }

      .btn-text {
        margin-right: 35px;
      }
    }
  }

  &.btn-rounded {
    &.left-label {
      .btn-label {
        border-bottom-left-radius: 60px;
        border-top-left-radius: 60px;
      }
    }

    &.right-label {
      .btn-label {
        border-bottom-right-radius: 60px;
        border-top-right-radius: 60px;
      }
    }
  }

  &.btn-outline {
    &.btn-lable-wrap {
      .btn-label {
        background: transparent;
      }
    }
  }

  &.btn-anim {
    position: relative;
    overflow: hidden;

    i {
      font-size: 16px;
      height: 100%;
      left: 0;
      padding-top: 10px;
      position: absolute;
      top: -100%;
      @include transition(all 0.3s ease 0s);
      width: 100%;
    }

    .btn-text {
      position: relative;
      top: 0;
      @include transition(all 0.3s ease 0s);
    }

    &:hover {
      i {
        top: 0;
      }

      .btn-text {
        top: 50px;
      }
    }
  }

  &.btn-icon-anim {
    position: relative;
    overflow: hidden;
    z-index: 1;
    border: none;
    padding: 11px;
    background: transparent;

    &:active,
    &:focus,
    &:hover {
      background: transparent;
      border: none;
    }

    i {
      font-size: 16px;
    }

    &:before {
      content: "";
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      @include transition(color 0.3s ease 0s,box-shadow 0.3s ease 0s, transform 0.3s ease 0s);
      width: 100%;
      z-index: -1;
    }

    &.btn-default {
      &:before {
        box-shadow: 0 0 0 35px rgb(220,220,220) inset;
      }

      &:hover {
        color: $black!important;

        &:before {
          box-shadow: 0 0 0 2px rgb(220,220,220) inset;
