/* Top Navigation */
.navbar {
  .brand-img {
    margin: 15px 0 0;
  }

  &.navbar-inverse {
    background: $white;

    &.navbar-fixed-top {
      border-bottom: 1px solid #e5e5e5;
      margin-bottom: 0;
      min-height: 59px;
      padding: 0 15px;

      .nav {
        > li {
          &.dropdown {
            position: relative;
          }

          > a {
            color: #5c5c5c;
            background: transparent!important;
            @include transition (.3s ease);
            padding: 0 9px;
            line-height: 59px;

            &:active,
            &:focus,
            &:hover {
              color: $black;
            }

            .user-auth-img {
              height: 43px;
              width: 43px;
              vertical-align: middle;
            }

            .user-online-status {
              background: $green;
              border-radius: 50%;
              bottom: 10px;
              height: 10px;
              border: 2px solid $white;
              position: absolute;
              right: 0;
              width: 10px;
            }
          }

          .dropdown-menu {
            margin-top: 10px;

            &:before {
              border-bottom: 10px solid $white;
              border-left: 10px solid transparent;
              border-right: 10px solid transparent;
              content: "";
              display: inline-block;
              margin-left: -6px;
              position: absolute;
              right: 8px;
              top: -10px;
            }

            &:after {
              border-bottom: 11px solid #eee;
              border-left: 11px solid transparent;
              border-right: 11px solid transparent;
              content: "";
              display: inline-block;
              position: absolute;
              right: 7px;
              top: -12px;
              z-index: -1;
            }

            &.app-dropdown {
              min-width: 300px;
              padding: 15px;

              .connection-item {
                display: inline-block;
                padding-bottom: 10px;
                padding-top: 10px;
                text-align: center;
                color: $grey;
                text-transform: capitalize;
                width: 75px;

                i {
                  font-size: 35px;
                }

                &:hover {
                  color: $black;
                  background: $light_grey;
                }
              }

              .app-icon-wrap {
                text-align: center;

                > li {
                  display: inline-block;
                }
              }
            }

            &.alert-dropdown {
              min-width: 300px;

              .streamline {
                height: 320px;
                padding: 15px;
                position: relative;

                .sl-item {
                  &:before {
                    display: none;
                  }

                  .icon {
                    position: absolute;
                    font-size: 25px;
                    left: -13px;
                    top: -5px;
                  }
                }
              }
            }

            &.user-auth-dropdown {
              min-width: 175px;
              text-transform: capitalize;
            }
          }
        }

        .open {
          > a {
            background: $white;

            &:active,
            &:focus,
            &:hover {
              background: $white;
            }
          }
        }

        &.top-nav {
          > li {
            display: inline-block;
            float: left;

            > a {
              i.top-nav-icon {
                font-size: 18px;
                vertical-align: middle;
              }

              .top-nav-icon-badge {
                background: $pink;
                border-radius: 50%;
                color: $white;
                font-size: 10px;
                font-family: $font_head;
                height: 16px;
                line-height: 16px;
                position: absolute;
                right: 0;
                text-align: center;
                top: 16px;
                width: 16px;
              }
            }
          }
        }

        &.navbar-right {
          margin-right: 0;
        }
      }

      .navbar-search-overlap {
        background-color: #fff;
        left: 0;
        position: absolute !important;
        right: 0;
        top: 0;

        input {
          border: medium none !important;
          min-height: 59px;
        }
      }

      .navbar-right .dropdown-menu {
        left: auto;
        right: 0;
      }
    }
  }
}

