            background: transparent;

            .icon-ac-wrap {
              .minus-ac {
                opacity: 1;
              }

              .plus-ac {
                opacity: 0;
              }
            }

            a {
              color: #8f8f8f;
            }
          }

          a {
            color: #8f8f8f;
          }
        }
      }
