table.dataTable.stripe tbody tr.odd {
  background-color: inherit;
}

table.dataTable.display tbody td,
table.dataTable.display tbody th,
table.dataTable.row-border tbody td,
table.dataTable.row-border tbody th {
  border-color: $light_grey;
  border-left: none;
  border-bottom: none;
}

table.dataTable > tfoot > tr > th {
  border-bottom: 0 solid $light_grey !important;
  border-left: medium none !important;
}

table.dataTable {
  border-right: medium none !important;
}

.table > colgroup + thead > tr:first-child > th,
table.dataTable > caption + thead > tr:first-child > td,
table.dataTable > caption + thead > tr:first-child > th,
table.dataTable > colgroup + thead > tr:first-child > td,
table.dataTable > thead:first-child > tr:first-child > td,
table.dataTable > thead:first-child > tr:first-child > th {
  border-left: medium none !important;
}

.dataTables_wrapper {
  .dataTables_filter input,
  .dataTables_length select {
    @extend .form-control;
  }

  .dataTables_filter,
  .dataTables_length {
    margin-bottom: 30px;
  }
}

.dataTables_wrapper .dataTables_paginate .paginate_button.current,
.dataTables_wrapper .dataTables_paginate .paginate_button.current:hover,
.dataTables_wrapper .dataTables_paginate .paginate_button:active,
.dataTables_wrapper .dataTables_paginate .paginate_button:focus,
.dataTables_wrapper .dataTables_paginate .paginate_button:hover {
  background: $green;
  border-color: $green;
  color: $white !important;
  box-shadow: none;
}

.dataTables_wrapper .dataTables_filter,
.dataTables_wrapper .dataTables_info,
.dataTables_wrapper .dataTables_length,
.dataTables_wrapper .dataTables_paginate,
.dataTables_wrapper .dataTables_processing {
  color: $black;
}

.sorting,
.sorting_asc,
.sorting_desc {
  position: relative;
}

table.dataTable thead .sorting::after {
  content: "\f0dc";
  opacity: 0.2;
}

table.dataTable thead .sorting_asc::after {
  content: "\f0de";
}

table.dataTable thead .sorting_desc::after {
  content: "\f0dd";
}

table.dataTable thead .sorting::after,
table.dataTable thead .sorting_asc::after,
table.dataTable thead .sorting_desc::after {
  display: block;
  font-family: FontAwesome;
  position: absolute;
  right: 10px;
  top: 15px;
  font-size: 14px;
}

table.dataTable thead .sorting,
table.dataTable thead .sorting_asc,
table.dataTable thead .sorting_desc {
  background: none;
  border-left: medium none;
  border-top: medium none;
}

table.footable > tbody > tr > td.footable-sortable,
table.footable > tbody > tr > th.footable-sortable,
table.footable > tfoot > tr > td.footable-sortable,
table.footable > tfoot > tr > th.footable-sortable,
table.footable > thead > tr > td.footable-sortable,
table.footable > thead > tr > th.footable-sortable {
  padding: 16px;
  border-top: none;
}

table.footable > thead > tr.footable-filtering > th,
table.footable > tr.footable-filtering > th {
  border: none!important;
}

tr.footable-header > th {
  border-top: none!important;
}

.footable-editing {
  .btn {
    &.footable-delete,
    &.footable-edit {
      background: transparent;
      border: none!important;

      .fooicon.fooicon-pencil {
        color: $green;
      }

      .fooicon.fooicon-trash {
        color: $red;
      }
    }
  }
}

.footable-show {
  .fooicon.fooicon-pencil {
    padding-right: 6px;
  }
}

.footable-filtering {
  .checkbox {
    margin-bottom: inherit;
    margin-top: inherit;
    min-height: inherit;
    padding: 3px 20px !important;
    font-family: $font_main;
    text-transform: capitalize;

    input[type="checkbox"] {
      opacity: 1;
    }

    label::before {
      display: none;
    }
  }
}

.fooicon.fooicon-sort,
.fooicon.fooicon-sort-asc,
.fooicon.fooicon-sort-desc {
  font-family: FontAwesome!important;
  font-size: 14px;
}

.fooicon.fooicon-sort {
  &:before {
    content: "\f0dc";
  }
}

.fooicon.fooicon-sort-asc {
  &:before {
    content: "\f0de";
  }
}

.fooicon.fooicon-sort-desc {
  &:before {
    content: "\f0dd";
  }
}

table.footable > tbody > tr.footable-empty > td {
  font-size: 18px;
}

