/*Morris Chart*/
.morris-hover.morris-default-style {
  background: rgba(47,44,44,.9);
  border: none;
  border-radius: 0;
  color: $black;
  font-family: $font_main;
  font-size: 12px;

  .morris-hover-row-label {
    color: $white;
    font-weight: 400;
  }
}
/*Flot Chart*/
.flot-container {
  width: 100%;
  height: 400px;

  .legendColorBox > div {
    border: none!important;
  }
}

.demo-placeholder {
  width: 100%;
  height: 100%;
}

.flotTip {
  padding: 8px 12px;
  background: rgba(47,44,44,.9);
  z-index: 100;
  color: $white;
  font-size: 12px;
}

.legendLabel {
  font-family: $font_main;
  color: $black;
  font-size: 12px;
  padding-left: 9px;
}
/*Chartist*/
.ct-series-a .ct-bar,
.ct-series-a .ct-line,
.ct-series-a .ct-point,
.ct-series-a .ct-slice-donut {
  stroke: $yellow;
}

.ct-series-b .ct-bar,
.ct-series-b .ct-line,
.ct-series-b .ct-point,
.ct-series-b .ct-slice-donut {
  stroke: $green;
}

.ct-series-c .ct-bar,
.ct-series-c .ct-line,
.ct-series-c .ct-point,
.ct-series-c .ct-slice-donut {
  stroke: $blue;
}

.ct-series-d .ct-bar,
.ct-series-d .ct-line,
.ct-series-d .ct-point,
.ct-series-d .ct-slice-donut {
  stroke: $pink;
}

.ct-series-c .ct-area,
.ct-series-c .ct-slice-pie {
  fill: $green;
}

.ct-series-a .ct-area,
.ct-series-a .ct-slice-pie {
  fill: $blue;
}

.ct-series-b .ct-area,
.ct-series-b .ct-slice-pie {
  fill: $yellow;
}

.ct-series-d .ct-area,
.ct-series-d .ct-slice-pie {
  fill: $red;
}

.ct-area {
  fill-opacity: 0.8;
}

.ct-grid {
  stroke: $light_grey;
  stroke-dasharray: 0;
  stroke-width: 1px;
}

.ct-label {
  color: $black;
  fill: $white;
  font-family: $font_main;
}

.ct-chart-bar .ct-label.ct-horizontal.ct-end {
  font-family: $font_main;
  color: #black;
}
/*Easy pie chart*/
.easypiechart {
  display: inline-block;
  height: 100px;
  position: relative;
  text-align: center;
  width: 100px;

  canvas {
    left: 0;
    position: absolute;
    top: 0;
  }

  .percent {
    color: $black;
    display: inline-block;
    font-size: 14px;
    line-height: 100px;
    z-index: 2;

    &:after {
      content: "%";
      font-size: 14px;
      margin-left: 1px;
    }
  }
}

.easypiechart.easypiechart-widget-pie {
  margin-bottom: 5px;
  margin-top: 5px;
}

.pie-chart > span {
  left: 0;
  margin-top: -2px;
  position: absolute;
  right: 0;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}

.easypiechart > span > img {
  height: 60%;
  left: 0;
  margin: 0 auto;
  position: absolute;
  right: 0;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  width: 60%;
}
/*Skills Counter*/
.skill-head {
  font-family: $font_head;
  font-size: 10px;
  text-transform: uppercase;
  display: block;
  text-align: center;

  &.skill-head-inside {
    line-height: 100px;
  }
}

.skill-circle {
  display: block;
  margin-left: auto;
  margin-right: auto;

  &.skill-circle-fill {
    background: #f7f7f9;
    border-radius: 50%;
  }
}
/*Nestable*/
.dd-handle {
  @extend .form-control;
  font-weight: 400;
  margin: 0 0 15px;
  font-size: 14px;
  padding-top: 10px;
  background: transparent;
  color: $grey;

  &:active,
  &:focus,
  &:hover {
    color: $black;
  }
}

.dd-item > button {
  margin: 0;
  height: 42px;
}

.dd-placeholder {
  background: transparent;
  border-color: $light_grey;
}

.dd3-content {
  display: block;
  @extend .form-control;
  color: $grey;
  margin: 0 0 15px;
  font-size: 14px;
  padding: 10px 10px 5px 52px;

  &:active,
  &:focus,
  &:hover {
    color: $black;
  }
}

.dd-dragel > .dd3-item > .dd3-content {
  margin: 0;
}

.dd3-item > button {
  margin-left: 30px;
}

.dd3-handle {
  position: absolute;
  margin: 0;
  left: 0;
  top: 0;
  cursor: pointer;
  width: 42px;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;

  &:before {
    font-family: FontAwesome;
    content: "\f047";
    color: $grey;
    display: block;
    position: absolute;
    left: 0;
    top: 10px;
    width: 100%;
    text-align: center;
    text-indent: 0;
  }
}
/*Treeview*/
.treeview {
  .list-group {
    margin-bottom: 0;

    .list-group-item {
      border: 1px solid $light_grey;
      height: 42px;
      padding: 10px 12px;

      &:hover {
        background: #f7f7f9;
      }

      &:first-child,
      &:last-child {
        border-radius: 0;
      }
    }
  }
}
/*Modals*/
.modal-dialog {
  .modal-content {
    border-radius: 0;
  }

  .modal-footer {
    border: none;
  }
}
/*Sweet-alert*/
.sweet-alert {
  border-radius: 0;
  font-family: inherit;

  h2 {
    font-weight: 400;
    color: $black;
  }

  button {
    @extend .btn;
    box-shadow: none!important;
    font-size: inherit;
  }
}
/*Notification*/
.alert {
  border-radius: 0;
  border: none;
  color: $white;

  &.alert-success {
    background: rgb(60,184,120);
  }

  &.alert-info {
    background: rgb(234,101,162);
  }

  &.alert-danger {
    background: rgb(241,91,38);
  }

  &.alert-warning {
    background: rgb(252,176,59);
  }

  &.alert-style-1 {
    padding-left: 70px;
    background: $white;
    position: relative;
    color: $black;
    border: 1px solid $light_grey;

    i {
      height: 100%;
      left: 0;
      margin: 0;
      padding-top: 20px;
      position: absolute;
      text-align: center;
      top: 0;
      width: 50px;
      color: $white;
    }

    &.alert-success {
      i {
        background: rgb(60,184,120);
      }
    }

    &.alert-info {
      i {
        background: rgb(234,101,162);
      }
    }

    &.alert-danger {
      i {
        background: rgb(241,91,38);
      }
    }

    &.alert-warning {
      i {
        background: rgb(252,176,59);
      }
    }
  }
}

.jq-toast-wrap {
  .jq-toast-single {
    color: $white;
    border-radius: 0;
    margin: 0;
    font-family: $font_main;

    .jq-toast-loader {
      border-radius: 0;
    }

    .jq-toast-heading {
      font-size: 16px!important;
      font-family: $font_head;
    }

    &.jq-has-icon {
      padding: 13px 10px 13px 50px;

      &:before {
        position: absolute;
        font-family: "themify";
        font-size: 22px;
        left: 12px;
        top: 13px;
      }
    }

    &.jq-icon-info {
      background: rgb(234,101,162);

      &:before {
        content: "\e717";
      }
    }

    &.jq-icon-error {
      background: rgb(241,91,38);

      &:before {
        content: "\e620";
      }
    }

    &.jq-icon-success {
      background: rgb(60,184,120);

      &:before {
        content: "\e64c";
      }
    }

    &.jq-icon-warning {
      background: rgb(252,176,59);

      &:before {
        content: "\e6c5";
      }
    }
  }
}

.bottom-center-fullwidth {
  .jq-toast-wrap {
    left: 0!important;
    right: 0!important;
    width: 100%!important;
    bottom: 0!important;
  }
}

.top-center-fullwidth {
  .jq-toast-wrap {
    left: 0!important;
    right: 0!important;
    width: 100%!important;
    top: 0!important;
  }
}
/*Tooltop*/
.tooltip-inner {
  border-radius: 0;
  font-family: $font_main;
}
/*Icons*/
.icon-container {
  overflow: hidden;
  padding-bottom: 20px;
  padding-top: 20px;
  position: relative;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  @include transition(.3s ease);

  &:hover {
    color: $black;
  }

  i,
  span.glyphicon {
    margin-right: 20px;
    font-size: 18px;
  }
}

.checkbox {
  label {
    &:before {
      border-radius: 0;
    }
  }
}

.checkbox input[type="checkbox"]:focus + label::before,
.checkbox input[type="radio"]:focus + label::before,
.radio input[type="radio"]:focus + label::before {
  outline: none;
}

.checkbox-primary input[type="checkbox"]:checked + label::before,
.checkbox-primary input[type="radio"]:checked + label::before {
  background-color: rgb(86,111,201);
  border-color: rgb(86,111,201);
}

.checkbox-danger input[type="checkbox"]:checked + label::before,
.checkbox-danger input[type="radio"]:checked + label::before {
  background-color: rgb(241,91,38);
  border-color: rgb(241,91,38);
}

.checkbox-info input[type="checkbox"]:checked + label::before,
.checkbox-info input[type="radio"]:checked + label::before {
  background-color: rgb(234,101,162);
  border-color: rgb(234,101,162);
}

.checkbox-warning input[type="checkbox"]:checked + label::before,
.checkbox-warning input[type="radio"]:checked + label::before {
  background-color: rgb(252,176,59);
  border-color: rgb(252,176,59);
}

.checkbox-success input[type="checkbox"]:checked + label::before,
.checkbox-success input[type="radio"]:checked + label::before {
  background-color: rgb(60,184,120);
  border-color: rgb(60,184,120);
}

.checkbox-primary input[type="checkbox"]:indeterminate + label::before,
.checkbox-primary input[type="radio"]:indeterminate + label::before {
  background-color: rgb(86,111,201);
  border-color: rgb(86,111,201);
}

.checkbox-danger input[type="checkbox"]:indeterminate + label::before,
.checkbox-danger input[type="radio"]:indeterminate + label::before {
  background-color: rgb(241,91,38);
  border-color: rgb(241,91,38);
}

.checkbox-info input[type="checkbox"]:indeterminate + label::before,
.checkbox-info input[type="radio"]:indeterminate + label::before {
  background-color: rgb(234,101,162);
  border-color: rgb(234,101,162);
}

.checkbox-warning input[type="checkbox"]:indeterminate + label::before,
.checkbox-warning input[type="radio"]:indeterminate + label::before {
  background-color: rgb(252,176,59);
  border-color: rgb(252,176,59);
}

.checkbox-success input[type="checkbox"]:indeterminate + label::before,
.checkbox-success input[type="radio"]:indeterminate + label::before {
  background-color: rgb(60,184,120);
  border-color: rgb(60,184,120);
}

.radio-primary input[type="radio"] + label::after {
  background-color: rgb(86,111,201);
}

.radio-primary input[type="radio"]:checked + label::before {
  border-color: rgb(86,111,201);
}

.radio-primary input[type="radio"]:checked + label::after {
  background-color: rgb(86,111,201);
}

.radio-danger input[type="radio"] + label::after {
  background-color: rgb(241,91,38);
}

.radio-danger input[type="radio"]:checked + label::before {
  border-color: rgb(241,91,38);
}

.radio-danger input[type="radio"]:checked + label::after {
  background-color: rgb(241,91,38);
}

.radio-info input[type="radio"] + label::after {
  background-color: rgb(234,101,162);
}

.radio-info input[type="radio"]:checked + label::before {
  border-color: rgb(234,101,162);
}

.radio-info input[type="radio"]:checked + label::after {
  background-color: rgb(234,101,162);
}

.radio-warning input[type="radio"] + label::after {
  background-color: rgb(252,176,59);
}

.radio-warning input[type="radio"]:checked + label::before {
  border-color: rgb(252,176,59);
}

.radio-warning input[type="radio"]:checked + label::after {
  background-color: rgb(252,176,59);
}

.radio-success input[type="radio"] + label::after {
  background-color: rgb(60,184,120);
}

.radio-success input[type="radio"]:checked + label::before {
  border-color: rgb(60,184,120);
}

.radio-success input[type="radio"]:checked + label::after {
  background-color: rgb(60,184,120);
}
/*File Upload*/
.fileupload {
  overflow: hidden;
  position: relative;
}

.fileupload input.upload {
  cursor: pointer;
  filter: alpha(opacity=0);
  font-size: 20px;
  margin: 0;
  opacity: 0;
  padding: 0;
  position: absolute;
  right: -3px;
  top: -3px;
  padding: 5px;
}
/*File Manager*/
.file-box {
  float: left;
  width: 220px;
}

.file-manager {
  h5 {
    text-transform: uppercase;
  }
  list-style: none outside none;
  margin: 0;
  padding: 0;
}

.folder-list li {
  a {
    color: #666666;
    display: block;
    padding: 5px 0;
  }
  border-bottom: 1px solid #e7eaec;
  display: block;

  i {
    margin-right: 8px;
    color: #3d4d5d;
  }
}

.category-list li {
  a {
    color: #666666;
    display: block;
    padding: 5px 0;
  }
  display: block;

  i {
    margin-right: 8px;
    color: #3d4d5d;
  }
}

.file-manager h5.tag-title {
  margin-top: 20px;
}

.tag-list li {
  float: left;

  a {
    font-size: 12px;
    background-color: $light_grey;
    padding: 5px 12px;
    border: 1px solid $light_grey;
    margin-right: 5px;
    margin-top: 5px;
    display: block;
  }
}

.file {
  border: 1px solid #e7eaec;
  padding: 0;
  background-color: $white;
  position: relative;
  margin-bottom: 20px;
  margin-right: 20px;
}

.file-manager .hr-line-dashed {
  margin: 15px 0;
}

.file {
  .icon,
  .image {
    height: 100px;
    overflow: hidden;
  }

  .icon {
    padding: 15px 10px;
    display: table;
    width: 100%;
    text-align: center;
  }
}

.file-control {
  color: inherit;
  font-size: 13px;
  margin-right: 10px;

  &.active {
    text-decoration: underline;
  }
}

.file {
  .icon i {
    color: $blue;
    display: table-cell;
    font-size: 30px;
    vertical-align: middle;
  }

  .file-name {
    padding: 10px;
    background-color: #f7f7f9;
    border-top: 1px solid #f7f7f9;

    span {
      font-size: 12px;
      color: $grey;
    }
  }
}

.file-name small {
  color: $grey;
}

ul.tag-list li {
  list-style: none;
}

a.compose-mail {
  padding: 8px 10px;
}

.mail-search {
  max-width: 300px;
}

.ibox {
  clear: both;
  margin-bottom: 25px;
  margin-top: 0;
  padding: 0;

  &.collapsed {
    .ibox-content {
      display: none;
    }

    .fa {
      &.fa-chevron-up:before {
        content: "\f078";
      }

      &.fa-chevron-down:before {
        content: "\f077";
      }
    }
  }

  &:after,
  &:before {
    display: table;
  }
}

.ibox-title {
  -moz-border-bottom-colors: none;
  -moz-border-left-colors: none;
  -moz-border-right-colors: none;
  -moz-border-top-colors: none;
  background-color: $white;
  border-color: #e7eaec;
  border-image: none;
  border-style: solid solid none;
  border-width: 3px 0 0;
  color: inherit;
  margin-bottom: 0;
  padding: 14px 15px 7px;
  min-height: 48px;
}

.ibox-content {
  background-color: $white;
  color: inherit;
  padding: 15px 20px 20px;
}

.ibox-footer {
  color: inherit;
  border-top: 1px solid $light_grey;
  font-size: 90%;
  background: $white;
  padding: 10px 15px;
}
/*Carousel*/
.carousel-control {
  &.left,
  &.right {
    background: none;
    text-shadow: none;
  }

  &:hover {
    &.left,
    &.right {
      background: none;
      text-shadow: none;
    }
  }

  span {
    display: inline-block;
    position: absolute;
    top: 50%;
    z-index: 5;
  }
}

.carousel-caption {
  p {
    font-family: $font_head;
  }
}

.carousel-indicators {
  li {
    border-color: $grey;
    background: $grey;
    opacity: 0.6;

    &:hover {
      background: $white;
    }
  }

  li,
  li.active {
    height: 8px;
    width: 8px;
    border-radius: 100%;
  }

  li.active {
    border: 1px solid $white;
    margin: 1px;
    opacity: 1;
  }
}

.text-slider {
  .carousel-indicators {
    li {
      &:hover {
        background: $green;
        opacity: 1;
      }
    }

    li.active {
      border: 1px solid $green;
      background: $green;
    }
  }
}

.owl-theme .owl-nav [class*="owl-"] {
  @extend .btn;
  @extend .btn-default;
  @extend .btn-xs;
}

.owl-theme .owl-dots .owl-dot span {
  height: 8px;
  margin: 5px 3px;
  width: 8px;
  background: $grey;
  opacity: 0.6;
}

.owl-theme .owl-dots .owl-dot.active span,
.owl-theme .owl-dots .owl-dot:hover span {
  opacity: 1;
  background: $green;
}
/*Testimonial slider*/
.testimonial-slider-wrap {
  .carousel-indicators {
    bottom: -50px;
  }
}
/*Twitter slider*/
.twitter-icon-wrap {
  i {
    font-size: 30px;
    color: $black;
  }
}

.twitter-slider-wrap {
  .carousel-indicators {
    bottom: -50px;
  }
}
/*Range Slider*/
.irs-bar,
.irs-bar-edge,
.irs-line-left,
.irs-line-mid,
.irs-line-right,
.irs-slider {
  background: url("../img/range/sprite-skin-modern.png");
}

.irs-slider {
  background-position: 0 -120px;
}

.irs-bar {
  background-position: 0 -60px;
}

.irs-from,
.irs-single,
.irs-to {
  font-size: 10px;
  padding: 4px 8px;
  background: $black;
  border-radius: 4px;
  border-radius: 0;
}

.irs-from::after,
.irs-single::after,
.irs-to::after {
  border-color: $black transparent transparent;
}

.irs-grid-text {
  font-size: 10px;
}
/*Gallery*/
.project-gallery {
  a {
    display: block;
    overflow: hidden;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    z-index: 1;
    width: 100%;
    position: relative;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    @include transition(all 0.4s ease 0s);

    img {
      @include transform(translateY(0));
      @include transition(all 0.4s ease 0s);
      image-rendering: optimizeQuality;
    }

    .hover-cap {
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 400;
      background: $white none repeat scroll 0 0;
      bottom: 0;
      color: black;
      height: 65px;
      padding-top: 25px;
      left: 0;
      overflow: hidden;
      perspective: 1px;
      position: absolute;
      text-align: center;
      text-transform: uppercase;
      @include transform(translateY(75px));
      @include transition(all 0.4s ease 0s);
      width: 100%;
    }

    &:hover {
      box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.2);
      -webkit-box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.2);
      -moz-box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.2);

      img {
        @include transform(translateY(-20px));
      }

      .hover-cap {
        @include transform(translateY(0));
      }
    }
  }
}

.lg-sub-html h6 {
  margin-bottom: 0;
}

#filters {
  li {
    margin-right: 5px;

    a {
      background: #f7f7f9 none repeat scroll 0 0;
      border-radius: 60px;
      color: #8f8f8f;
      display: inline-block;
      padding: 10px 20px;
      text-transform: capitalize;
      font-size: 14px;

      &.active,
      &:active,
      &:focus,
      &:hover {
        background: $green;
        color: $white;
      }
    }
  }

  &:last-child {
    margin-right: 0;
  }
}
/*Typography*/
.grid-block {
  [class^="col-"] {
    padding-bottom: 10px;
    padding-top: 10px;
    border: 1px solid $light_grey;
    border-radius: 0;
    box-shadow: none!important;
    color: $grey;
    height: 42px;
  }
}
/*Panel*/
.panel-title {
  @extend h6;
}

.panel {
  .panel-heading {
    a {
      position: relative;

      i {
        @include transition(opacity .3s ease);

        &.fa-angle-down {
          opacity: 1;
          position: absolute;
          top: 1px;
        }

        &.fa-angle-up {
          opacity: 0;
        }
      }

      &.collapsed {
        i {
          &.fa-angle-down {
            opacity: 0;
          }

          &.fa-angle-up {
            opacity: 1;
          }
        }
      }
    }
  }

  .panel-footer {
    background-color: transparent;
    border-top: 1px solid $light_grey;
    padding: 15px;
  }
}

.panel-info > .panel-heading {
  background-color: $pink;
}

.panel-danger > .panel-heading {
  background-color: $red;
}

.panel-warning > .panel-heading {
  background-color: $yellow;
}

.panel-success > .panel-heading {
  background-color: $green;
}

.panel-primary > .panel-heading {
  background-color: $blue;
}

.panel-inverse > .panel-heading {
  background-color: rgb(220,220,220);
  color: $black!important;
}

.panel-danger > .panel-heading,
.panel-info > .panel-heading,
.panel-inverse > .panel-heading,
.panel-primary > .panel-heading,
.panel-success > .panel-heading,
.panel-warning > .panel-heading {
  border: none;
  margin: -15px -15px 0;
  padding: 15px 30px;
  border-radius: 0;
}

.well {
  border-color: $light_grey;
  border-radius: 0;
  box-shadow: none;
  padding: 30px;
  background: #f7f7f9;

  &.well-lg {
    padding: 35px;
  }

  &.well-sm {
    padding: 25px;
  }
}
/*Pricing Table*/
.panel-pricing {
  background: #f7f7f9;
  border-radius: 0;
  @include transition(.3s ease);

  &:hover {
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05);
  }

  .list-group-item {
    background: transparent;
    border: none;
    border-radius: 0;
  }

  .panel-body {
    hr {
      border-color: $light_grey;
      border-width: 2px;
    }

    .list-group {
      hr {
        border-width: 1px;
      }

      .list-group-item {
        i {
          color: $green;
          padding-right: 15px;
        }
      }
    }
  }

  .panel-heading {
    i {
      display: block;
      font-size: 50px;
      margin-bottom: 25px;
      margin-top: 50px;
      color: $black;
    }

    .panel-price {
      font-family: $font_head;
      font-size: 60px;
      color: $black;
      font-weight: 700;
      display: block;
      margin-top: 20px;
      text-transform: capitalize;

      .pricing-dolor {
        display: inline-block;
        font-size: 18px;
        padding-top: 10px;
        vertical-align: top;
      }
    }
  }

  .panel-footer {
    border: none;

    .btn {
      font-size: 14px;
    }
  }
}
/*Dropdown*/
.show-on-hover:hover > ul.dropdown-menu {
  display: block;
}

.dropdown-menu {
  border-radius: 0;
  border-color: $light_grey;

  > li {
    > a {
      padding: 5px 20px;
    }
  }

  .divider {
    background-color: $light_grey;
  }

  > .active {
    > a {
      background: $green;

      &:focus,
      &:hover {
        background: $green;
      }
    }
  }
}

.dropdown-menu.animated {
  -webkit-animation-duration: 0.55s;
  animation-duration: 0.55s;
  -webkit-animation-timing-function: ease;
  animation-timing-function: ease;
}
/*Weather*/
.forcast-days > li {
  display: inline-block;
  font-size: 12px;
  margin-right: 15px;
  text-align: center;

  &:last-child {
    margin-right: 0;
  }

  span.forcast-high-deg {
    color: $red;
  }

  img {
    width: 50px;
  }
}

.temprature {
  font-size: 60px;
  font-family: $font_head;
  color: $black;

  .unit {
    display: inline-block;
    font-size: 14px;
    padding-top: 10px;
    vertical-align: top;
  }
}

.currently {
  font-size: 16px;
  color: $black;
}

.other-details {
  border-top: 1px solid $light_grey;
  border-bottom: 1px solid $light_grey;
  margin: 15px 0;
  padding: 15px 0;

  li {
    display: table;
    width: 100%;

    span {
      display: table-cell;
      padding-bottom: 5px;
      padding-top: 5px;
      text-transform: capitalize;
      width: 25%;

      &:nth-child(2),
      &:nth-child(4) {
        color: $black;
        text-transform: capitalize;
      }
    }
  }
}

.left-block-wrap,
.right-block-wrap {
  min-height: 175px;
}

.right-block-wrap {
  text-align: center;

  .right-block {
    .temprature-icon img {
      width: 80px;
    }
  }
}

.left-block-wrap {
  .left-block {
    .temprature {
      font-size: 40px;
    }
  }
}

.weather-inverse {
  .panel-body {
    background: #f7f7f9;
  }

  .left-block-wrap {
    background: $white;
  }
}

.weather-info {
  .left-block-wrap {
    background: $pink;
    color: $white!important;

    .temprature,
    h5 {
      color: $white!important;
    }
  }
}

.weather-warning {
  .left-block-wrap {
    background: $yellow;
    color: $white!important;

    .temprature,
    h5 {
      color: $white!important;
    }
  }
}

.weather-danger {
  .left-block-wrap {
    background: $red;
    color: $white!important;

    .temprature,
    h5 {
      color: $white!important;
    }
  }
}

.weather-success {
  .left-block-wrap {
    background: $green;
    color: $white!important;

    .temprature,
    h5 {
      color: $white!important;
    }
  }
}
/*Sparkline*/
.jqstooltip {
  width: auto !important;
  height: auto !important;
  background: rgba(47,44,44,.9)!important;
  border: none!important;
}
/*Pagination*/
.pagination {
  > li {
     > span,
    > a {
      border-color: $light_grey;
      border-radius: 0!important;
      color: $green;

      &:active,
      &:focus,
      &:hover {
        border-color: $light_grey;
        background-color: #f2f2f2;
      }
    }

    &.active {
       > span,
      > a {
        background: $green;
        border-color: $light_grey;

        &:active,
        &:focus,
        &:hover {
          background: $green;
          border-color: $light_grey;
        }
      }
    }
  }
}

.pagination > .disabled > a,
.pagination > .disabled > a:focus,
.pagination > .disabled > a:hover,
.pagination > .disabled > span,
.pagination > .disabled > span:focus,
.pagination > .disabled > span:hover {
  border-color: $light_grey;
  color: $green;
}

.pagination > li > a:focus,
.pagination > li > a:hover,
.pagination > li > span:focus,
.pagination > li > span:hover {
  color: $grey;
}
/*Pager*/
.pager li > a,
.pager li > span {
  border: 2px solid $light_grey;
  border-radius: 60px;
}
/*Labels*/
.label {
  padding: 5px 10px;
  font-size: 11px;
  text-transform: capitalize;
  border-radius: 0;
  font-weight: 400;

  &.label-info {
    background: $pink;
  }

  &.label-primary {
    background: $blue;
  }

  &.label-success {
    background: $green;
  }

  &.label-danger {
    background: $red;
  }

  &.label-warning {
    background: $yellow;
  }
}
/*Badge*/
.badge {
  background-color: #DCDCDC;
  font-weight: 400;
  padding: 5px 10px;
  color: $black;

  &.badge-info {
    background: $pink;
    color: $white;
  }

  &.badge-primary {
    background: $blue;
    color: $white;
  }

  &.badge-success {
    background: $green;
    color: $white;
  }

  &.badge-danger {
    background: $red;
    color: $white;
  }

  &.badge-warning {
    background: $yellow;
    color: $white;
  }
}
/*Task Panel*/
.task-panel {
  .list-group-item {
    background-color: $white;
    border-color: $light_grey;
    border-image: none;
    border-radius: 0 !important;
    border-style: solid none;
    border-width: 1px medium;
    padding: 15px;

    i {
      color: $grey;
      padding-right: 10px;
    }
  }
}
/*Vecotor Map*/
.jvectormap-tip {
  @extend .label;
}
/*Progressbar*/
.progress {
  -webkit-box-shadow: none !important;
  background-color: rgba(120, 130, 140, 0.13);
  box-shadow: none !important;
  height: 6px;
  border-radius: 50px;
  margin-bottom: 18px;
  overflow: hidden;

  .progress-bar {
    box-shadow: none;
    font-size: 8px;
    font-weight: 600;
    line-height: 12px;

    &:last-child {
      border-radius: 0 60px 60px 0;
    }
  }

  &.progress-xs {
    height: 5px !important;
  }

  &.progress-sm {
    height: 8px !important;
  }

  &.progress-md {
    height: 15px !important;

    .progress-bar {
      font-size: 8px;
      font-weight: 400;
      line-height: 14.4px;
    }
  }

  &.progress-lg {
    height: 20px !important;

    .progress-bar {
      font-size: 10px;
      font-weight: 400;
      line-height: 20px;
    }
  }
}

.progress-bar-info {
  background: $pink;
}

.progress-bar-primary {
  background: $blue;
}

.progress-bar-success {
  background: $green;
}

.progress-bar-danger {
  background: $red;
}

.progress-bar-warning {
  background: $yellow;
}

.progress-vertical {
  min-height: 250px;
  height: 250px;
  width: 8px;
  position: relative;
  display: inline-block;
  margin-bottom: 0;
  margin-right: 20px;

  .progress-bar {
    width: 100%;

    &:last-child {
      border-radius: 0 60px 60px;
    }
  }
}

.progress-vertical-bottom {
  min-height: 250px;
  height: 250px;
  position: relative;
  width: 8px;
  display: inline-block;
  margin-bottom: 0;
  margin-right: 20px;

  .progress-bar {
    width: 100%;
    position: absolute;
    bottom: 0;

    &:last-child {
      border-radius: 60px 60px 60px 0!important;
    }
  }
}

.progress-vertical-bottom.progress-sm,
.progress-vertical.progress-sm {
  width: 8px !important;
}

.progress-vertical-bottom.progress-sm .progress-bar,
.progress-vertical.progress-sm .progress-bar {
  font-size: 8px;
  line-height: 5px;
}

.progress-vertical-bottom.progress-md,
.progress-vertical.progress-md {
  width: 15px !important;
}

.progress-vertical-bottom.progress-md .progress-bar,
.progress-vertical.progress-md .progress-bar {
  font-size: 10.8px;
  line-height: 14.4px;
}

.progress-vertical-bottom.progress-lg,
.progress-vertical.progress-lg {
  width: 20px !important;
}

.progress-vertical-bottom.progress-lg .progress-bar,
.progress-vertical.progress-lg .progress-bar {
  font-size: 12px;
  line-height: 20px;
}

.progress-bar-striped,
.progress-striped .progress-bar {
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent) !important;
  background-size: 40px 40px !important;
}
/*Search Bar*/
.app-search {
  margin: 0;
  position: relative;

  a {
    color: #4c5667;
    position: absolute;
    right: 10px;
    top: 15px;
  }

  .form-control,
  .form-control:focus {
    background: rgba(255, 255, 255, 0.9) none repeat scroll 0 0;
    border: medium none;
    border-radius: 40px;
    box-shadow: none;
    color: #4c5667;
    display: inline-block;
    font-size: 13px;
    font-weight: 600;
    height: 30px;
    line-height: 30px;
    margin-top: 10px;
    padding-left: 20px;
    padding-right: 40px;
    @include transition(all 0.5s ease-out 0s);
    width: 180px;

    &:focus {
      width: 300px;
      @extend .form-control;
    }
  }
}
/*Contact Cards*/
.contact-card {
  .card-user-name {
    color: $black;
    text-transform: uppercase;
    font-family: $font_head;
    font-size: 13px;
  }

  .card-user-email,
  .card-user-phone {
    color: $grey;
  }
}
/*Contact List*/
.page-aside {
  &.contact-list {
    position: relative;

    .left-aside {
      background: $white;
    }
  }
}
/*Chat UI*/
.chat-list-wrap {
  .chat-body {
    > a {
      border-bottom: 1px solid $light_grey;
      display: block;
    }

    .chat-data {
      padding: 15px;
      @include transition(.3s ease);

      &:hover {
        background: #f7f7f9;
      }

      .user-img {
        height: 40px;
        width: 40px;
        float: left;
        margin-right: 25px;
      }

      .user-data {
        float: left;

        .time {
          font-size: 12px;
          margin-top: 2px;
        }
      }

      .status {
        border-radius: 50%;
        float: right;
        height: 7px;
        position: relative;
        top: 5px;
        width: 7px;
        margin-top: 10px;

        &.away {
          background: $yellow;
        }

        &.offline {
          background: $red;
        }

        &.online {
          background: $green;
        }
      }
    }
  }
}

.goto-chat-list {
  position: absolute!important;
}

.chat-content {
  .user-img {
    height: 27px;
    width: 27px;
    position: absolute;
  }

  ul {
    li {
      margin-bottom: 30px;
    }
  }

  .msg {
    padding: 10px 15px;
    color: $black;

    .msg-per-detail {
      span {
        font-size: 12px;
        text-transform: capitalize;
      }
    }
  }

  .friend {
    .friend-msg-wrap {
      .msg {
        margin-left: 37px;
        background: #f7f7f9;
      }
    }
  }

  .self {
    .self-msg-wrap {
      .msg {
        background: rgba(60,184,120,.2);
        margin-left: 100px;
      }
    }
  }
}

.self-input {
  position: relative;

  input {
    border: none!important;
    padding-right: 65px;
  }

  a {
